header#mysavei-title {
  display: flex;
  flex-direction: column;
  align-items: center;
}
header#mysavei-title .title-dot {
  height: 9px;
  width: 9px;
  background-color: #52B788;
  border: 2px solid #F6AE2D;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 18px;
  transform: translate(57px, 0) translateZ(0);
  -webkit-transform: translate(57px, 0) translateZ(0);
  -moz-transform: translate(57px, 0) translateZ(0);
  z-index: 5;
}
header#mysavei-title h1 {
  color: #fff;
  font-weight: 700 !important;
  font-size: 2rem !important;
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.16);
  padding-bottom: 1rem;
  padding-top: 1rem;
}
